import JPushModule from 'jpush-react-native';
import {Platform} from 'react-native'
import JPushAndroid from './android/JPushAndroid'
import JPushIOS from './ios/JPushIOS';

const phoneDevice = Platform.OS;
const JPushUtils = phoneDevice=='ios'?JPushIOS:JPushAndroid;

//获取 registrationId，这个 JPush 运行通过 registrationId 来进行推送
JPushUtils.getRegisID =(regisId,callback)=>{
  JPushModule.getRegistrationID((regisId) => {
    callback && callback();
  })
};

//
JPushUtils.setAlias = (str,callback) =>{
  JPushModule.setAlias(str, (success) => {
    callback && callback();
  })
};

//添加
JPushUtils.addTags = (arr,callback) =>{
  JPushModule.addTags(arr,(success) => {
    callback && callback();
  })
};

//设置标签
JPushUtils.setTags = (arr,callback) =>{
  JPushModule.setTags(arr,(success) => {
    console.log(success)
    callback && callback();
  })
};

//删除
JPushUtils.deleteTags = (arr,callback) =>{
  JPushModule.deleteTags(arr, (success) => {
    callback && callback();
  })
};

//清除所有标签
JPushUtils.cleanTags = (callback) =>{
  JPushModule.cleanTags((success) => {
    callback && callback();
  })
};

export default JPushUtils;