import React,{Component} from 'react';

export function createInitScreen(didMount,renderInit){
  return class MainScreen extends Component{
    constructor(props){
      super(props);
    }

    componentDidMount() {
      const {dispatch} = this.props;
      if(this.props.authenticated){
        didMount && didMount(dispatch)
      }
    }

    render(){
      let {isLoggedIn} = this.props;
      return renderInit(isLoggedIn)
    }
  };
}
