import React,{Component} from 'react';
import {
  View,
  Text,
  Modal,
  StyleSheet,
  ImageBackground,
  TouchableOpacity,
  Alert
} from 'react-native';

import DeviceInfo from 'react-native-device-info'
import AppUpdate from 'react-native-appupdate';


export function InitAppUpdate(appUpdateImg,appid,apkUrl){

  return class AppUpgradeVersion extends Component{
    constructor(props){
      super(props);
      this.state = {
        modalVisible:false,
        showLoading:false,
        appid,
        apkUrl,
        lineWidth:1,
        version:this.props.versionName || ''
      };
    }

    componentDidMount() {
        console.log(this.state.version)
        if(this.state.version && this.state.version!=DeviceInfo.getVersion()){
          if(DeviceInfo.getVersion() != this.state.version){
            this.setState({modalVisible:true})
          }else{
            this.setState({modalVisible:false})
          }
        }
    }

    setModalVisible(visible) {
      this.setState({modalVisible: visible});
    }

    updateApp(){
      console.log(this.state.apkUrl)
      this.setState({showLoading:true});
      const appUpdate = new AppUpdate({
        iosAppId: this.state.appid,
        apkVersionUrl: this.state.apkUrl,//'https://github.com/version.json',
        needUpdateApp: (needUpdate) => {
          needUpdate(true)
        },
        forceUpdateApp: () => {
          console.log("Force update will start")
        },
        notNeedUpdateApp: () => {
          console.log("App is up to date")
        },
        downloadApkStart: () => { console.log("Start") },
        downloadApkProgress: (progress) => {
          this.setState({
            progress:progress,
            lineWidth:scaleSize(progress*3)
          });
        },
        downloadApkEnd: () => {
          this.setState({
            progress:'100',
            lineWidth:scaleSize(300)
          });
        },
        onError: () => { console.log("downloadApkError") }
      });
      appUpdate.checkUpdate();
    }

    render(){
      return(
        <Modal
            animationType={"fade"}
            transparent={true}
            visible={this.state.modalVisible}
        >
          <View style={styles.UpgradeVersion}>
            <ImageBackground source={appUpdateImg} style={styles.UpgradePic} >
              <View style={styles.updateInfo}>
                <Text style={styles.updateTipText}>立即升级!</Text>
                <Text style={styles.updateTipText}>系统更新至 {this.state.version||'2.0.1'} 版本</Text>
              </View>
              <View style={{alignItems:'center'}}>
                {
                  this.state.showLoading == false ?
                    <TouchableOpacity activeOpacity={0.7} style={styles.updateAppBtn} onPress={()=>{this.updateApp()}}>
                      <Text style={{color:'#fff'}}>立即升级</Text>
                    </TouchableOpacity>
                  :
                  <View style={styles.downloadStyle} >
                    <Text style={{fontSize:12}}>APP下载{this.state.progress}%...</Text>
                    <View style={styles.downloadLongWrap}>
                      <View style={[styles.downloadLong,{width:this.state.lineWidth}]}></View>
                    </View>
                  </View>
                }
              </View>
            </ImageBackground>
          </View>
        </Modal>
      )
    }
  }
}

const styles=StyleSheet.create({
  UpgradeVersion:{
    flex:1,
    top:0,
    left:0,
    backgroundColor:'rgba(0,0,0,0.4)',
    position:'absolute',
    width:deviceWidth,
    height:deviceHeight,
    zIndex:9999,
    justifyContent:'center',
    alignItems:'center'
  },
  appCodePop:{
    left:'50%',
    top:'50%',
    marginLeft:-scaleSize(560/2),
    marginTop:-scaleSize(260/2+180),
    width:scaleSize(560),
    height:scaleSize(560),
    backgroundColor:'#ebebeb',
    borderRadius:4,
  },
  UpgradePic:{
    width:scaleSize(509),
    height:scaleSize(825),
  },
  updateInfo:{
    paddingTop:scaleSize(580),
    paddingHorizontal:scaleSize(80)
  },
  updateTipText:{
    fontSize:15,
    color:'#999',
    paddingVertical:scaleSize(4)
  },
  updateAppBtn:{
    marginTop:scaleSize(36),
    width:scaleSize(200),
    height:scaleSize(70),
    backgroundColor:'#53a7fd',
    justifyContent:'center',
    alignItems:'center'
  },
  downloadStyle:{
    marginTop:scaleSize(30),
    height:scaleSize(70),
    justifyContent:'center',
    alignItems:'center'
  },
  downloadLongWrap:{
    marginTop:scaleSize(20),
    width:scaleSize(300),
    height:scaleSize(20),
    justifyContent:'center',
    alignItems:'flex-start',
    backgroundColor:'#ebebeb'
  },
  downloadLong:{
    height:scaleSize(20),
    backgroundColor:'#53a7fd'
  }
});
