import React, { Component } from 'react';
import {
  StatusBar,
  View,
  BackHandler
} from 'react-native'
import { addNavigationHelpers, StackNavigator, NavigationActions} from 'react-navigation';

export function createRouteScreen(screen){
  return StackNavigator(screen);
}

export function createLoginAfter(screenOption){

  const AppNavigator = createRouteScreen(screenOption);

  return class AppWithNavigationState extends Component{
    constructor(props){
      super(props);
    }

    componentDidMount() {
      BackHandler.addEventListener("hardwareBackPress", this.onBackPress);
    }

    componentWillUnmount() {
      BackHandler.removeEventListener("hardwareBackPress", this.onBackPress);
    }

    onBackPress = () => {
      const { dispatch, nav } = this.props;
      if (nav.index === 0) {
        return false;
      }
      dispatch(NavigationActions.back());
      return true;
    };

    render(){
      let {dispatch, nav} = this.props;
      return (
          <View style={{flex:1}}>
            <StatusBar
                animated={true}
                backgroundColor="rgba(0,0,0,0)"
                translucent={true}
                barStyle="light-content"
                showHideTransition='fade'
            />
            <AppNavigator navigation={addNavigationHelpers({ dispatch, state: nav })} />
          </View>
      )
    }
  }
}