import React, { Component } from 'react';
import {
  Platform,
  StyleSheet,
  Text,
  View
} from 'react-native';
//import App from './src/index'

export function createApp(didMount,renderInit){
  return class App extends Component{
    constructor(props){
      super(props);
      this.state={
        isLoading:false,
        authenticated:false,
      }
    }

    componentDidMount() {
      didMount && didMount(this)
    }

    render() {
      if(!this.state.isLoading){
        return null;
      }
      return (renderInit && renderInit(this))

    }
  }
}